// out: ../css/login.min.css, compress: true, strictMath: true

.etc-login-form {
    color: #919191;
    padding: 10px 20px;
    p {
        margin-bottom: 5px;
    }
}
.login-form-1 {
    max-width: 300px;
    border-radius: 5px;
    display: inline-block;
    .form-control {
        border: 0;
        box-shadow: 0 0 0;
        border-radius: 0;
        background: transparent;
        color: #555555;
        padding: 7px 0;
        font-weight: bold;
        height:auto;
    }
    .form-control::-webkit-input-placeholder {
        color: #999999;
    }
    .form-group {
        margin-bottom: 0;
        border-bottom: 2px solid #efefef;
        padding-right: 20px;
        position: relative;
    }
    .form-group:last-child {
        border-bottom: 0;
    }
    .login-button {
        position: absolute;
        right: -25px;
        top: 50%;
        background: #ffffff;
        color: #999999;
        padding: 11px 0;
        width: 50px;
        height: 50px;
        margin-top: -25px;
        border: 5px solid #efefef;
        border-radius: 50%;
        overflow: hidden;
        white-space: nowrap;
        padding-right: 7px;
        transition: background ease-in-out 200ms;
        i{
            font-weight: bold;
            transition: all ease-in-out 200ms;
            position: relative;
            margin-left: -57px;
            &:first-child{
                margin-right: 100px;
            }
        }
    }
    .login-button:hover {
        color: #fff;
        background: #B20F19;
        //        transform:  rotate(360deg);
        i{

            margin-left: 8px;
        }

    }
    .login-button.clicked {
        color: #555555;
    }
    .login-button.clicked:hover {
        transform: none;
    }
    .login-button.clicked.success {
        color: #2ecc71;
    }
    .login-button.clicked.error {
        color: #e74c3c;
    }
}
.main-login-form {
    position: relative;
}
.login-form-1 .form-control:-moz-placeholder,.login-form-1 .form-control::-moz-placeholder,.login-form-1 .form-control:-ms-input-placeholder {
    color: #999999;
}
.login-group {
    background: #ffffff;
    color: #999999;
    border-radius: 8px;
    padding: 10px 20px;
}
.login-group-checkbox {
    padding: 5px 0;
}
label.form-invalid {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    display: block;
    margin-top: -25px;
    padding: 7px 9px;
    background: #777777;
    color: #ffffff;
    border-radius: 5px;
    font-weight: bold;
    font-size: 11px;
}
label.form-invalid:after {
    top: 100%;
    right: 10px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: transparent;
    border-top-color: #777777;
    border-width: 6px;
}
.login-form-main-message {
    background: #ffffff;
    color: #999999;
    border-left: 3px solid transparent;
    border-radius: 3px;
    margin-bottom: 8px;
    font-weight: bold;
    height: 0;
    padding: 0 20px 0 17px;
    opacity: 0;
    transition: all ease-in-out 200ms;
}
.login-form-main-message.show {
    height: auto;
    opacity: 1;
    padding: 10px 20px 10px 17px;
}
.login-form-main-message.success {
    border-left-color: #2ecc71;
}
.login-form-main-message.error {
    border-left-color: #e74c3c;
}
[type="checkbox"]:not(:checked),[type="checkbox"]:checked,[type="radio"]:not(:checked),[type="radio"]:checked {
    position: absolute;
    left: -9999px;
}
[type="checkbox"]:not(:checked) + label,[type="checkbox"]:checked + label,[type="radio"]:not(:checked) + label,[type="radio"]:checked + label {
    position: relative;
    padding-left: 25px;
    padding-top: 1px;
    cursor: pointer;
}
[type="checkbox"]:not(:checked) + label:before,[type="checkbox"]:checked + label:before,[type="radio"]:not(:checked) + label:before,[type="radio"]:checked + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 2px;
    width: 17px;
    height: 17px;
    border: 0px solid #aaa;
    background: #f0f0f0;
    border-radius: 3px;
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3);
}
[type="checkbox"]:not(:checked) + label:after,[type="checkbox"]:checked + label:after,[type="radio"]:not(:checked) + label:after,[type="radio"]:checked + label:after {
    position: absolute;
    color: #555555;
    transition: all .2s;
}
[type="checkbox"]:not(:checked) + label:after,[type="radio"]:not(:checked) + label:after {
    opacity: 0;
    transform: scale(0);
}
[type="checkbox"]:checked + label:after,[type="radio"]:checked + label:after {
    opacity: 1;
    transform: scale(1);
}
[type="checkbox"]:disabled:not(:checked) + label:before,[type="checkbox"]:disabled:checked + label:before,[type="radio"]:disabled:not(:checked) + label:before,[type="radio"]:disabled:checked + label:before {
    box-shadow: none;
    border-color: #8c8c8c;
    background-color: #878787;
}
[type="checkbox"]:disabled:checked + label:after,[type="radio"]:disabled:checked + label:after {
    color: #555555;
}
[type="checkbox"]:disabled + label,[type="radio"]:disabled + label {
    color: #8c8c8c;
}
[type="checkbox"]:checked:focus + label:before,[type="checkbox"]:not(:checked):focus + label:before,[type="checkbox"]:checked:focus + label:before,[type="checkbox"]:not(:checked):focus + label:before {
    border: 1px dotted #f6f6f6;
}
label:hover:before {
    border: 1px solid #f6f6f6 !important;
}
[type="checkbox"]:not(:checked) + label:before,[type="checkbox"]:checked + label:before {
    border-radius: 3px;
}
[type="radio"]:not(:checked) + label:before,[type="radio"]:checked + label:before {
    border-radius: 35px;
}
[type="checkbox"]:not(:checked) + label:after,[type="checkbox"]:checked + label:after {
    content: 'âœ”';
    top: 0;
    left: 2px;
    font-size: 14px;
}
[type="radio"]:not(:checked) + label:after,[type="radio"]:checked + label:after {
    content: '\2022';
    top: 0;
    left: 3px;
    font-size: 30px;
    line-height: 25px;
}
.logo {
    padding: 15px 0;
    font-size: 25px;
    color: #fff;
    font-weight: bold;
}
